import { Box, Text, Image, Link } from "@chakra-ui/react";
import fitness from "../../src/images/fitness.png";
import Aos from 'aos';
import "aos/dist/aos.css";
import { useEffect } from "react";

function Project3() {

  useEffect(()=>{
    Aos.init({duration: 2000});
   },[])

  return (
    <Box
    data-aos="flip-right"
      margin={"auto"}
      mb="80px"
      w="80%"
      justifyContent="center"
      alignContent={"center"}
      className="project"
    >
      <Box textAlign="center">
        <Text
          color={"#FFB6C1"}
          fontSize={{ base: "21px", md: "27px", lg: "38px" }}
          pt={30}
        >
          Fitness Blender - Clone
        </Text>
      </Box>
      <Box
        alignItems={"center"}
        justifyContent={"center"}
        display={{ lg: "flex" }}
        textAlign="center"
      >
        <Image
          ml={{ lg: "30px" }}
          pt={10}
          m="auto"
          w={{ base: "80%", md: "70%", lg: "47%" }}
          src={fitness}
        ></Image>
        <Box mt={50}>
          <Text
            color={"#FFB6C1"}
            fontSize={{ base: "18px", md: "23px", lg: "32px" }}
          >
            Description
          </Text>
          <Text
            margin="auto"
            mb={10}
            w="80%"
            fontSize={{ base: "12px", md: "16px", lg: "20px" }}
            mt={5}
          >
            This is a clone of the Fitness Blender website. The clone is
            complete. with the login, sign-in, shopping cart, and workout pages
            Program Pages and Payment Pages are available.
          </Text>
          <Box>
            <Text
              color={"#FFB6C1"}
              fontSize={{ base: "18px", md: "23px", lg: "32px" }}
              mb={10}
            >
              Tech Stacks
            </Text>
            <Box
              fontSize={{ base: "12px", md: "16px", lg: "20px" }}
              justifyContent={"space-evenly"}
              display={"flex"}
              gap="1"
            >
              <Text>ReactJS</Text>
              <Text>Chakara-ui</Text>
              <Text>NodeJS</Text>
              <Text>MongoDB</Text>
            </Box>
          </Box>
          <Box mt={"60px"} display={"flex"} justifyContent="space-around">
            <Link
              target="_blank"
              href="https://github.com/HimanshuSingh1407/Fitness-blender-clone"
            >
              <Image
                w={{ base: "22px", md: "30px", lg: "40px" }}
                src="https://img.icons8.com/3d-fluency/2x/github.png"
              ></Image>
            </Link>
            <Link target="_blank" href="https://fitnesscenter1.netlify.app/">
              <Image
                w={{ base: "20px", md: "26px", lg: "30px" }}
                src="https://cdn.iconscout.com/icon/free/png-64/netlify-3628945-3030170.png"
              ></Image>
            </Link>
          </Box>
        </Box>
      </Box>
    </Box>
  );
}

export default Project3;
